import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			payType: 1,
			orderNo: '',
			pOrderNo: '',
			order: {},
			suscribeMsgList:[]
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'footPrint', 'applicationConfig'])
	},
	onLoad(options) {
		this.orderNo = options.orderNo;		//非拆分订单
		this.pOrderNo = options.pOrderNo;	//拆分订单
		if(this.orderNo)
			this.inquiryOrder(this.orderNo);
		else if(this.pOrderNo)
			this.inquiryPOrder(this.pOrderNo);
		this.inquirySuscribeMsg();
	},

	methods: {
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirmWx: function() {
			uni.requestSubscribeMessage({
				// 订单模板消息
				tmplIds: this.suscribeMsgList,
				success: function(res) {
					console.log('订单模板消息订阅成功');
				}
			});
			if (this.payType === 1) {
				this.wechatPay();
			} else if (this.payType === 2) {
				this.alipayPay();
			} else if (this.payType === 3) {
				this.balancePay();
			}

		},
		//确认支付
		confirm: function() {
			if (this.payType === 1) {
				this.wechatPay();
			} else if (this.payType === 2) {
				this.alipayPay();
			} else if (this.payType === 3) {
				this.balancePay();
			}
		
		},
		//余额支付
		wechatPay() {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes.code);
					that.$api.request.wechatPay({
						orderNo: that.orderNo,
						pOrderNo: that.pOrderNo,
						code: loginRes.code
					}, res => {
						if (res.body.status.statusCode === '0') {
							var data = res.body.data;
							console.log(res.body.data);
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: data.timestamp,
								nonceStr: data.nonceStr,
								package: data.packageStr,
								signType: 'MD5',
								paySign: data.signature,
								success: function(res) {
									uni.redirectTo({
										url: '/pages/money/paySuccess'
									})
								},
								fail: function(err) {
									console.log('fail:' + JSON.stringify(err));
								}
							});
						} else {
							console.log(res.body.status.errorDesc);
						}
					});
				}
			});
		},
		//支付宝支付
		alipayPay() {
			uni.redirectTo({
				url: '/pages/money/paySuccess'
			})
		},
		//余额支付
		balancePay() {
			this.$api.request.balancePay({
				orderNo: this.orderNo,
				pOrderNo: this.pOrderNo,
				paymentMethod: this.payType
			}, res => {
				if (res.body.status.statusCode === '0') {
					uni.redirectTo({
						url: '/pages/money/paySuccess'
					})
				} else {
					this.$api.msg(res.body.status.errorDesc);
				}
			});
		},
		//查询订单
		inquiryPOrder(pOrderNo) {
			this.$api.request.orderPaymentAmount({
				pOrderNo: pOrderNo
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.order = res.body.data;
				} else {
					console.log(res.body.status.errorDesc);
				}
			});
		},
		//查询订单
		inquiryOrder(orderNo) {
			this.$api.request.orderDetail({
				orderNo: orderNo
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.order = res.body.data;
				} else {
					console.log(res.body.status.errorDesc);
				}
			});
		},
		//查询订阅消息
		inquirySuscribeMsg() {
			this.$api.request.wxSuscribeMsg({}, res => {
				if (res.body.status.statusCode === '0') {
					this.suscribeMsgList = res.body.data.suscribeMsgList;
				} else {
					console.log(res.body.status.errorDesc);
				}
			});
		}
		
	}
}
